import React from 'react';
import PropTypes from 'prop-types';

function finder__toggle(props) {
    const randomNumber = Math.random()
        .toString(16)
        .slice(-4);

    const setFacet = e => {
        const newQuery = props.query,
            newValue = e.target.value,
            checked = e.target.checked;

        checked
            ? (newQuery.facets[props.facet.meta] = newValue)
            : delete newQuery.facets[props.facet.meta];
        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const idString = `${props.facet.name}-${randomNumber}`;

    return (
        <fieldset className="finder__filter finder__togglebox">
            <div className="finder__toggle">
                <label htmlFor={idString}>
                    {props.facet.values[0].label}
                    <input
                        type="checkbox"
                        id={idString}
                        name={props.facet.name}
                        value={props.facet.values[0].data}
                        onChange={e => setFacet(e)}
                        checked={
                            props.query.facets[props.facet.meta] ? true : false
                        }
                    />
                    <span
                        className={`toggle ${
                            props.query.facets[props.facet.meta]
                                ? 'toggle--open'
                                : 'toggle--close'
                        }`}
                    />
                </label>
            </div>
        </fieldset>
    );
}

finder__toggle.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
};

export default finder__toggle;
